.page {
   width: 100%;
   max-width: 600px;
   margin: 0 auto;
   padding: 0;
   border: 0;
   border-radius: 10px;
}

.header {
}

h1 {
   padding: 20px;
   margin: auto;
}

.card {
   background-color: cornflowerblue;
   border-radius: 20px;
}

.card-content {
   padding: 30px;
   width: 100%;
}

.title {
   font-size: 30px;
   text-align: center;
}

.form {
   width: 400px;
}

.input {
}

.check {
   /* background-color: white; */
}

.check label {
   color: white;
}

[type="checkbox"]:not(:checked),
[type="checkbox"]:checked {
   opacity: 1;
   position: relative;
   margin-right: 15px;
   /* padding: 15px; */
}

.action {
   width: 100%;
   text-align: center;
   padding: 20px;
}

.a-button {
   background-color: gray;
   width: 150px;
   text-align: center;
   border-radius: 10px;
   display: inline-block;
   color: white;
}

.b-button {
   /* background-color: gray; */
   width: 150px;
   text-align: center;
   border-radius: 10px;
   display: inline-block;
}
