.list {
   text-align: center;
}

.item {
   height: 300px;
   width: 300px;
   border: black solid 1px;
   border-radius: 10px;
   display: inline-block;
   background-color: yellow;
   padding: 5px;
   margin: 5px;
   text-align: right;
}

.item .link {
   background-color: lightseagreen;
   border-radius: 10px;
}
